<script>
	import { io } from "socket.io-client";
  import Sweetalert2 from "sweetalert2";
	import "animate.css";
  
  const klaytn = window.klaytn;
  const socket = io();
  let Init = { OwnerList100: [], LastUpdated: "", List: [], MyRank: "", MyWallet: "" };
  
  socket.on("QueryList", async (res) => {
    if(res["Initialized"]){
      Init["List"] = res["List"];
      Init["LastUpdated"] = res["Date"];
      Init["OwnerList100"] = Init["List"].slice(0, 100);
      
      for(let i=0;i<Init["List"].length;i++){
        let count = i;
        if(String(Init["List"][count]["Holder"]).toLowerCase() == Init["MyWallet"].toLowerCase()){
          Init["MyRank"] = "You are in the " + ( count + 1 ) + "th place.";
          break;
        }
      }
      if(Init["MyRank"] == ""){
        Init["MyRank"] = "You don't have any points.";
      }
    }
    if(!res["Initialized"]){
      Sweetalert2.fire("Server is not initialized yet.");
    }
  });
  
  window.onload = async() => {
    let Confirmed = await Sweetalert2.fire({
      title: "Would you like to connect your Kaikas wallet?",
      html: "You can link your Kaikas wallet,<br>or manually enter your wallet address.<br><br>If you don't want to enter your wallet address, please click outside this notification window.",
      icon: "info",
      showCancelButton: true,
      returnInputValueOnDeny: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: '#d33',
      confirmButtonText: 'Connect Kaikas Wallet',
      cancelButtonText: 'Connect Wallet Manually',
      showClass: { popup: "animate__animated animate__shakeX" }
    });
    if(Confirmed.isConfirmed){
      try{
        await klaytn.enable();
        Init["MyWallet"] = klaytn.selectedAddress;
        Sweetalert2.fire({
          title: "Wallet Connected.",
          icon: "success",
					timer: 1000,
          showConfirmButton: false,
          allowOutsideClick: false,
					showClass: { popup: "animate__animated animate__tada" }
        });
      } catch(e){
        Sweetalert2.fire("There was an error connecting to the Kaikas wallet!", "Please check if the Kaikas wallet is installed properly!");
      }
    }
    
    if(Confirmed.dismiss == "cancel"){
      const { value: WalletAddress } = await Sweetalert2.fire({
        title: 'Enter Wallet Address',
        input: 'text',
        showCancelButton: true,
        inputValidator: (value) => {
          try{
            if (!value) {
              return "Please write your Wallet Address!";
            }
            if(String(parseInt(value)) == "NaN") throw new Error("Wallet Address is not correct.");
            if(value.length != 42) throw new Error("Wallet Address is not correct.");
          } catch(e){
            return "Please write correct Wallet Address!";
          }
        }
      });
      if(String(WalletAddress) != "undefined") {
          Init["MyWallet"] = WalletAddress;
          Sweetalert2.fire({
            title: "Wallet Connected.",
            icon: "success",
            timer: 1000,
            showConfirmButton: false,
            allowOutsideClick: false,
            showClass: { popup: "animate__animated animate__tada" }
          });
        }
      }
      if(Init["MyWallet"] == ""){
        Init["MyRank"] = "Refresh the page to connect your wallet";
      }
    socket.emit("QueryList");
  }
</script>
<body>
  <div class="easter">
      <div class="inner-wrap">
          <div class="visual">
              <div class="visual-bx">
                  <h1>
                      <img src="Assets/txt_header_title.png" alt="">
                  </h1>
              </div>
          </div>
          <div class="easter-content on">
              <div class="easter-content-info">
                  <img src="Assets/txt_info.png" alt="">
              </div>
              <div class="ownership">
                  <h2>
                      <img src="Assets/txt_content_title.png" alt="">
                  </h2>
                  <div class="ownershop-content">
                      <div class="ownershop-message">
                          <div class="ownershop-message-pc">
                              Ownership points are awarded based on the number and value of Pastures / Klay Dragons you own.<br>
                              The total sum of points will be calculated and rewards will be given to the top 700 winners!
                          </div>
                      </div>
                      <h3>
                          <img src="Assets/txt_ranking_rewards.png" alt="">
                      </h3>
                      <div class="ranking-rewards">
                          <ul>
                              <li><p>1st</p><img src="Assets/img_ranking01.png" alt="" class="ranking01"><p>Ghost Egg(Epic)</p></li>
                              <li><p>2-100th</p><img src="Assets/img_ranking02.png" alt="" class="ranking02"><p>Rainbow Egg(Event)</p></li>
                              <li><p>101-700th</p><img src="Assets/img_ranking03.png" alt="" class="ranking03"><p>Mystery Egg(Normal)</p></li>
                          </ul>
                          <img src="Assets/img_ownership_character.png" alt="">
                      </div>
                      <div class="score-breakdown">
                          <h3>
                              <img src="Assets/txt_score_breakdown.png" alt="">
                          </h3>
                          <div class="score-list">
                              <ul>
                                  <li>
                                      <img src="Assets/ic_sheepfarm.png" alt="">
                                      <span>Sheepfarm</span>
                                  </li>
                                  <li>
                                      <img src="Assets/ic_frame.png" alt="">
                                      <span>Large 20 Pts</span>
                                  </li>
                                  <li>
                                      <img src="Assets/ic_frame.png" alt="">
                                      <span>Medium 15 Pts</span>
                                  </li>
                                  <li>
                                      <img src="Assets/ic_frame.png" alt="">
                                      <span>Small 10 Pts</span>
                                  </li>
                              </ul>
                              <ul>
                                  <li>
                                      <img src="Assets/ic_mysteryegg.png" alt="">
                                      <span>Klay Dragons</span>
                                  </li>
                                  <li>
                                    <img src="Assets/MysteryEgg_Legendary.png" alt="">
                                      <span>Legendary 25 Pts</span>
                                  </li>
                                  <li>
                                    <img src="Assets/MysteryEgg_Epic.png" alt="">
                                      <span>Epic 15 Pts</span>
                                  </li>
                                  <li>
                                    <img src="Assets/MysteryEgg_Rare.png" alt="">
                                      <span>Rare 10 Pts</span>
                                  </li>
                                  <li>
                                    <img src="Assets/MysteryEgg_Normal.png" alt="">
                                      <span>Normal 3 Pts</span>
                                  </li>
                              </ul>
                          </div>
                          <p>
                              <img src="Assets/img_egg.png" alt="">
                          </p>
                      </div>
                  </div>
              </div>
              <div class="my-ranking">
                  <h2>
                      <img src="Assets/txt_my_ranking.png" alt="">
                  </h2>
                  <div class="my-ranking-content">
                      {Init["MyRank"]}
                  </div>
              </div>
              <div class="board">
                  <div class="board-bx">
                      <div class="board-content">
                          <h2>
                              <img src="Assets/txt_board.png" alt="">
                          </h2>
                          <p>If a rank is tied, the winner will be selected according to who reached the position first.</p>
                          <div class="board-list">
                              <ul>
                                  {#each Init["OwnerList100"] as { Holder, Points }, i}
                                  <li>{i+1}. {Holder} <span>{Points} Pts</span></li>
                                  {/each}
                              </ul>
                          </div>
                          <div class="update">
                              <div>Update interval: 15 minutes</div>
                              <div>Last Updated : {Init["LastUpdated"]}</div>
                              <div>Snapshot : 2022-02-24 12:00:00 UTC</div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>
  </div>
</body>
